/* Reset & Base Styling */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Variables for Light & Dark Mode */
:root {
  --bg-color: #f5f5f5;
  --text-color: #333;
  --card-color: #ffffff;
  --button-color: #4caf50;
  --button-hover: #43a047;
  --delete-color: #ff4d4d;
}

.dark-mode {
  --bg-color: #1e1e1e;
  --text-color: #d4d4d4;
  --card-color: #2c2c2c;
  --button-color: #bb86fc;
  --button-hover: #9b59b6;
}

/* App Container */
.app {
  background: var(--card-color);
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  width: 400px;
  min-height: 650px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Title */
h1 {
  text-align: center;
  font-size: 2rem;
  color: var(--text-color);
  margin-bottom: 1rem;
}

/* Form Styling */
form {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  width: 100%;
  align-items: center;
}

form input {
  width: 90%;
  padding: 0.7rem;
  border: 2px solid var(--button-color);
  border-radius: 10px;
  outline: none;
  transition: all 0.3s ease;
}

form input:focus {
  border-color: var(--button-hover);
}

form button {
  background-color: var(--button-color);
  color: white;
  border: none;
  padding: 0.7rem 1.2rem; /* Adjusted padding to better fit the word 'Add' */
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: var(--button-hover);
}

/* Timer Section */
.timer {
  margin-top: 1rem;
  padding: 1rem;
  background-color: var(--button-color);
  color: white;
  border-radius: 10px;
  text-align: center;
  width: 100%;
}

.timer h2 {
  margin-bottom: 0.5rem;
}

.timer h3 {
  font-size: 2rem;
  font-weight: bold;
}

/* Task List */
ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

li {
  background: var(--card-color);
  padding: 1rem;
  border-radius: 10px;
  margin: 0.5rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

li span {
  flex: 1;
  cursor: pointer;
}

li.completed span {
  text-decoration: line-through;
  opacity: 0.6;
}

li.completed button.delete-btn .delete-text {
  text-decoration: none; /* Prevent line-through on the X */
  opacity: 1;
}

/* Buttons */
button {
  background-color: var(--button-color);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: var(--button-hover);
}

button.delete-btn {
  background-color: var(--delete-color);
  color: #333; /* Default color of the X */
  font-weight: bold; /* Make the X more prominent */
  font-size: 1.2rem; /* Increase the size for better visibility */
}

button.delete-btn:hover {
  background-color: #e63946;
  color: #ffffff; /* Change X to white on hover */
}

/* Flashing Notification */
.flashing {
  animation: flash 1s infinite;
  background-color: var(--delete-color) !important;
}

@keyframes flash {
  0% { opacity: 1; }
  50% { opacity: 0.4; }
  100% { opacity: 1; }
}
